.portifolio {
  width: 80%;
  height: 100%;
  padding: 1% 2%;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
}

h1, h2, h3, p, strong, a {
  font-family: 'Open Sans', sans-serif;
}

.content {
  margin: 1%;
  display: flex;
  flex-direction: column;
}

a {
  margin: 0.5em;
  cursor: pointer;
}

a:visited {
  font-family: 'Open Sans', sans-serif;
  color: black;
}

p {
  margin: 0.2em 0;
}

h1 {
  font-size: 1.8em;
  font-weight: 700;
  margin: 0 0 1% 0;
}

.backend, .frontend, .algoritms, .artigos {
  padding: 1% 3%;
}

.after {
  box-sizing: border-box;
  width: 7%;
  height: 0.2em;
  background: #149ddd;
  margin: 0;
}

.between {
  box-sizing: border-box;
  width: 50%;
  height: 0.15em;
  background: #149ddd;
  margin: 3% 0 4% 0;
}

h2 {
  font-size: 1.4em;
  font-weight: 700;
  margin: 1% 0 1% 0;
}

video {
  margin-top: 2%
}

img {
  width: 400px;
  margin: 2% 0;
}

.logos {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
}

.logos > .logo {
  height: 70px;
  margin: 2% 0;
  margin-left: 2%;
  width: auto;
}

.logos > .logo:nth-child(1) {
  margin: 0;
}

